import * as React from 'react'
import Link from 'react-router-dom/es/Link'
import Typography from '@material-ui/core/Typography/Typography'
import { withStyles } from '@material-ui/core'

const styles = {
  underlineOnHover: {
    '& > h6:hover': {
      textDecoration: 'underline !important',
    },
  },
}

class DisplayFolderChildren extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isFolder: !!this.props.article.children[0] }
  }

  render() {
    const { isFolder } = this.state
    const { article, classes } = this.props
    if (!isFolder) {
      return null
    }

    const childrenListSorted = article.children.sort((a, b) => {
      return a.order - b.order
    })

    return (
      <div className="folder-children-list">
        Ce dossier comprend les articles suivants :
        {childrenListSorted.map((child, index) => {
          return (
            <div key={index} className="folder-name">
              <Link
                color="primary"
                to={`/articles/${child.urlId}`}
                style={{ textDecoration: 'none', textAlign: 'left' }}
                className={classes.underlineOnHover}
              >
                <Typography variant="subtitle1">
                  {child.order + 1} - {child.title}
                </Typography>
              </Link>
            </div>
          )
        })}
      </div>
    )
  }
}

export default withStyles(styles)(DisplayFolderChildren)
