function initSiteName() {
  if (window.location.href.indexOf('supra.support-vision') !== -1) {
    return 'supra'
  }
  if (window.location.href.indexOf('super.support-vision') !== -1) {
    return 'super'
  }
  if (window.location.href.indexOf('supple.support-vision') !== -1) {
    return 'supple'
  }

  return 'supple'
}

export default class SiteService {
  static site = initSiteName()

  static getSite() {
    return this.site
  }
}
