import * as React from 'react'
import Link from 'react-router-dom/es/Link'
import Typography from '@material-ui/core/Typography/Typography'
import Icon from '@material-ui/core/Icon/Icon'
import ConditionalLink from '../common/conditional-link'
import baseBackendUrl from '../common/base-backend-url'
import Flex from '../common/flex'
import SnackbarContext from '../common/snackbar-context'
import axios from 'axios'
import { withStyles } from '@material-ui/core'

const styles = {
  underlineOnHover: {
    '& > div > p:hover, > h6:hover': {
      textDecoration: 'underline !important',
    },
  },
}

class DisplayFolderBrothers extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isChild: !!this.props.article.parent, lastArticle: null, nextArticle: null }
  }

  static contextType = SnackbarContext

  componentDidMount() {
    if (!this.state.isChild) {
      return
    }

    this.getBrothers()
  }

  getBrothers = async () => {
    try {
      const response = await axios.get(
        baseBackendUrl.getBaseBackendUrl() + '/articles/' + this.props.article.urlId + '/brothers'
      )

      const prevArticle = response.data.find(article => {
        return article.order === this.props.article.order - 1
      })
      const nextArticle = response.data.find(article => {
        return article.order === this.props.article.order + 1
      })

      this.setState({ prevArticle: prevArticle, nextArticle: nextArticle })
    } catch (error) {
      this.context.displaySnackbar('Une erreur est survenue, veuillez recharger la page')
    }
  }

  render() {
    const { isChild, prevArticle, nextArticle } = this.state
    const { article, classes } = this.props

    if (!isChild) {
      return null
    }

    return (
      <div className="folder-brother-list">
        Cet article est contenu dans un dossier :
        <div className="folder-name">
          <Link
            color="primary"
            to={`/articles/${article.parent.urlId}`}
            style={{ textDecoration: 'none' }}
            className={classes.underlineOnHover}
          >
            <Typography variant="subtitle1">{article.parent.title}</Typography>
          </Link>
        </div>
        <div className="brother-list">
          <Flex justify="space-between" alignItems="center">
            <ConditionalLink
              condition={prevArticle}
              style={{ textDecoration: 'none' }}
              showLink={true}
              to={prevArticle ? prevArticle.urlId : null}
              className={classes.underlineOnHover}
            >
              <Flex justify="space-between" alignItems="center" style={{ marginRight: '1rem' }}>
                <Icon>arrow_back</Icon>
                <Typography variant="body1" style={{ textIndent: 'initial', marginTop: 0 }}>
                  {prevArticle ? prevArticle.title : "Pas d'article précédent"}
                </Typography>
              </Flex>
            </ConditionalLink>
            <ConditionalLink
              condition={nextArticle}
              style={{ textDecoration: 'none' }}
              showLink={true}
              to={nextArticle ? nextArticle.urlId : null}
              className={classes.underlineOnHover}
            >
              <Flex justify="space-between" alignItems="center" style={{ marginLeft: '1rem' }}>
                <Typography variant="body1" style={{ textIndent: 'initial', marginTop: 0 }}>
                  {nextArticle ? nextArticle.title : "Pas d'article suivant"}
                </Typography>
                <Icon>arrow_forward</Icon>
              </Flex>
            </ConditionalLink>
          </Flex>
        </div>
      </div>
    )
  }
}

export default withStyles(styles)(DisplayFolderBrothers)
