import Icon from '@material-ui/core/Icon/Icon'
import React from 'react'
import baseBackendUrl from '../common/base-backend-url'
import Fab from '@material-ui/core/Fab'
import SnackbarContext from '../common/snackbar-context'
import Flex from '../common/flex'
import axios from 'axios'

export default class DisplayArticleVotes extends React.Component {
  constructor(props) {
    super(props)

    this.state = { alreadyUpvoted: false, alreadyDownvoted: false }
  }

  static contextType = SnackbarContext

  componentDidMount() {
    const cookieVal = atob(localStorage.getItem('dldkljsfpngi'))
    const alreadyVoted = cookieVal.indexOf(this.props.article.id + '-')

    if (alreadyVoted !== -1) {
      cookieVal.substr(alreadyVoted + (String(this.props.article.id) + '-').length, 4) === 'true'
        ? this.setState({ alreadyUpvoted: true })
        : this.setState({ alreadyDownvoted: true })
    }
  }

  handleUpvote = async () => {
    const sens = this.state.alreadyUpvoted ? -1 : 1

    try {
      axios.put(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles/' +
          this.props.article.urlId +
          '/votes' +
          '?' +
          baseBackendUrl.getSiteParam() +
          '&dldkljsfpngi=' +
          sens +
          '&dldkljsfpngi2=1'
      )
      this.setState(prevState => ({
        alreadyUpvoted: !prevState.alreadyUpvoted,
      }))

      let cookieVal = atob(localStorage.getItem('dldkljsfpngi'))
      const alreadyVoted = cookieVal.indexOf(this.props.article.id + '-')

      if (alreadyVoted === -1) {
        cookieVal += this.props.article.id + '-true,'
      } else {
        cookieVal = cookieVal.replace(this.props.article.id + '-true,', '')
      }

      localStorage.setItem('dldkljsfpngi', btoa(cookieVal))
    } catch (error) {
      this.context.displaySnackbar('Une erreur est survenue, veuillez recharger la page')
    }
  }

  handleDownvote = async () => {
    const sens = this.state.alreadyDownvoted ? -1 : 1

    try {
      await axios.put(
        baseBackendUrl.getBaseBackendUrl() +
          '/articles/' +
          this.props.article.urlId +
          '/votes' +
          '?' +
          baseBackendUrl.getSiteParam() +
          '&dldkljsfpngi=' +
          sens +
          '&dldkljsfpngi2=-1'
      )
      this.setState(prevState => ({
        alreadyDownvoted: !prevState.alreadyDownvoted,
      }))

      let cookieVal = atob(localStorage.getItem('dldkljsfpngi'))
      const alreadyVoted = cookieVal.indexOf(this.props.article.id + '-')

      if (alreadyVoted === -1) {
        cookieVal += this.props.article.id + '-false,'
      } else {
        cookieVal = cookieVal.replace(this.props.article.id + '-false,', '')
      }

      localStorage.setItem('dldkljsfpngi', btoa(cookieVal))
    } catch (error) {
      this.context.displaySnackbar('Une erreur est survenue, veuillez recharger la page')
    }
  }

  render() {
    const { alreadyUpvoted, alreadyDownvoted } = this.state
    const { article } = this.props

    let colorUpvote = 'secondary'
    let colorDownvote = 'secondary'

    if (alreadyUpvoted) {
      colorUpvote = 'primary'
    }
    if (alreadyDownvoted) {
      colorDownvote = 'primary'
    }

    return (
      <Flex direction="row" wrap alignItems="center" justify="space-evenly">
        <h3 className="js-toc-ignore" style={{ marginRight: 10 }}>
          {article.children[0] ? <span>Ce dossier vous plaît </span> : <span>Cet article vous a plu </span>}?
        </h3>
        <Fab
          color={colorUpvote}
          size="medium"
          disabled={alreadyDownvoted}
          onClick={() => this.handleUpvote()}
          style={{ marginRight: 10 }}
        >
          <Icon>thumb_up</Icon>
        </Fab>
        <Fab color={colorDownvote} size="medium" disabled={alreadyUpvoted} onClick={() => this.handleDownvote()}>
          <Icon>thumb_down</Icon>
        </Fab>
      </Flex>
    )
  }
}
